.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;
  letter-spacing: 0.05em;

  &.--frozen {
      overflow: hidden;
  }
}

.container {
  width: 100%;
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

.text {
  line-height: 26px;
}


a {
  cursor: pointer;
  color: $link-color;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}



h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
  letter-spacing: -0.02em;
  color: #042951;
}

h1{
  font-weight: bold;
  font-size: 202px;
  line-height: 246px;
  letter-spacing: 0.02em;
}

h2{
  font-weight: bold;
  font-size: 80px;
  line-height: 97px;
  text-transform: uppercase;
}

h3{
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-transform: uppercase;
}

h4{
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
}

h5{
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

h6{
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.05em;
  font-weight: bold;
  text-transform: uppercase;
}