.main {
  padding-top: 161px;
  background: #042951 url(../img/main-bg.svg) no-repeat center bottom/ 100% auto;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 40px;
  color: #fff;

  h5{
    color: #fff;
    margin-bottom: -10px;
  }
  &__title{
    padding-top: 50px;
    margin-bottom: 150px;
    img{
      width: 100%;
    }
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
  }
  &__quote{
    font-weight: bold;
    font-size: 40px;
    line-height: 49px;
    align-self: flex-end;
    color: #fff;
  }
  &__info{
    max-width: 800px;
    align-self: flex-end;
    flex-wrap: wrap;
  }
  &__btn{
    margin-bottom: 43px;
    align-self: flex-end;
  }
  &__description{
    font-weight: 600;
    font-size: 24px;
    line-height: 130%;
    color: #fff;
    width: 100%;
    align-self: flex-end;
  }
}

.program{
  padding-top: 120px;
  &__title{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__ico-title{
    display: flex;
    align-items: center;
    &.--left{
      transform: translateX(-104px);
    }
  }
  .program__ico{
    margin-right: 24px;
  }
  &__item {
    padding-top: 60px;
    padding-bottom: 60px;
    display: flex;
    justify-content: space-between;
    &:not(:last-child){
      border-bottom: 1px solid rgba(#042951, .15);
    }
  }
  &__left{
    max-width: 450px;
    padding-right: 90px;
    width: 100%;
  }
  &__right{
    max-width: 670px;
    width: 100%;
  }

  &__day{
    margin-bottom: 8px;
  }

  &__date{
    color: #089FEB;
  }

  &__title-list{
    margin-bottom: 16px;
  }

  &__li{
    font-size: 20px;
    line-height: 140%;
    margin-bottom: 8px;
    padding-left: 18px;
    color: #042951;
    &:after{
      content: "";
      position: absolute;
      left: 0;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      top: 11px;
      background-color: #089FEB;
    }
  }
}

.section{
  padding-top: 60px;
  padding-bottom: 60px;
  h2{
    margin-bottom: 60px;
  }
}

.stream{
  padding: 60px 0;
  padding-bottom: 60px;
  &__item{
    display: flex;
    justify-content: space-between;
    &:not(:last-child){
      margin-bottom: 60px;
    }
  }
  &__name{
    width: 200px;
    padding-right: 24px;
  }
  &__video{
    width: calc(100% - 200px);
    iframe{
      width: 100%;
    }
  }
}

.platform{
  &__panel{
    background: url(../img/platform.svg) no-repeat right 60px bottom 60px / 437px auto;
    background-color:  #089FEB;
    border-radius: 20px;
    padding: 60px;
    color: #fff;
    display: flex;
    flex-wrap: wrap;
    min-height: 568px;
  }
  &__title{
    max-width: 720px;
    margin-bottom: 30px;
    color: #fff;
  }
  &__bottom{
    width: 100%;
    align-self: flex-end;
  }
  &__description{
    margin-bottom: 41px;
    color: #fff;
  }
  .btn{
    width: 100%;
    max-width: 260px;
  }
}

.direction{
  &__list{
    display: flex;
    margin-right: -50px;
    flex-wrap: wrap;
  }

  &__item{
    width: calc(25% - 50px);
    margin-right: 50px;
    margin-bottom: 66px;
  }
}

.organization{
  &__list{
    display: flex;
    justify-content: space-between;
  }
}

.company{
  padding-bottom: 120px;
}

.menu{
  display: none;
}


@media(max-width: 1300px){
  .main__info{
    max-width: 550px;
  }
}


@media(max-width: 980px){
  .main{
    padding-top: 130px;
    background-image: url(../img/main-bg-mobile.svg);
    padding-bottom: 44px;
    h3{
      margin-bottom: 75px;
    }
    h5{
      margin-bottom: 18px;
      max-width: 330px;
    }
  }
  .program {
    padding-top: 60px;
  }
  .program__left{
    margin-bottom: 37px;

  }
  .program__ico-title.--left{
    transform: none;
    margin-bottom: 0px;
  }
  .program__ico{
    width: 56px;
    margin-right: 16px;
  }
  .program__ico-info{
    width: calc(100% - 72px);
  }
  h2{
    font-size: 39px;
    line-height: 48px;
    margin-bottom: 20px;
  }
  .program__title{
    display: block;
  }
  .program__item{
    display: block;
  }
  .main__title{
    margin-bottom: 56px;
    padding-top: 0px;
  }
  .main__bottom{
    display: block;
  }
  .main__quote{
    margin-bottom: 53px;
  }
  .btn.--big,
  .btn.--small{
    height: 64px;
    width: 100%;
    max-width: 327px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 16px;
    line-height: 150%;
  }
  .btn.--big{
    margin-bottom: 32px;
  }
  .main__description {
    font-size: 16px;
    line-height: 130%;
  }
  h3{
    font-size: 25px;
    line-height: 30px;
    
  }
  .header__nav{
    display: none;
  }
  .header__burger{
    display: block;
  }
  .menu{
    position: fixed;
    left: 0;
    top: 90px;
    width: 100%;
    z-index: 200;
    background-color: rgba(4, 41, 81, 0.75);
    height: calc(100% - 90px);
  }
  .menu__panel{
    padding: 16px 24px 30px 24px;
    background-color: #042951;
  }
  .header__logo{
    width: 90px;
  }
  .nav__link{
    display: block;
    margin: 0;
    margin-bottom: 10px;
  }
  h5{
    font-size: 18px;
    line-height: 22px;
  }
  .stream__item {
    display: block;
    margin-bottom: 40px;
    h4{
      margin-bottom: 20px;
    }
  }
  .stream__name{
    width: 100%;
  }
  .stream__video{
    width: 100%;
  }
  .section{
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .platform__panel {
    background: #089FEB;
    padding: 60px 35px;
    img{
      margin-bottom: 52px;
    }
  }

  .direction__item{
    width: calc(50% - 50px);
  }
  .organization__scroll{
    overflow-y: auto;
    width: calc(100% + 40px);
    margin-left: -20px;
    padding-left: 20px;
  }
  .organization__list{
    padding-right: 30px;
    width: 800px;
  }
  .footer{
    padding-top: 60px;
  }
}


@media(max-width: 600px){
  .direction__list{
    margin-right: 0px;
  }
  .direction__item{
    width: 100%;
    margin-right: 0px;
    margin-bottom: 35px;
    img{
      width: 100%;
    }
  }
}