.footer{
  padding: 120px 0 30px;
  background-color: #042951;
  &__logo{
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
  }
  &__title{
    text-align: center;
    color: #fff;
    margin-bottom: 40px;
  }
}

.socials {
  display: flex;
  justify-content: center;
  margin-bottom: 120px;
  &__link{
    margin-left: 8px;
    margin-right: 8px;
  }
}

.copyright{
  font-size: 16px;
  line-height: 150%;
  color: #fff;
  opacity: .5;
  text-align: center;
}