.header{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 90px;
  padding-left: 40px;
  padding-right: 40px;
  color: #fff;
  z-index: 500;
  transition: background-color .4s;
  &.active{
    background-color: #042951;
  }
  &__burger{
    cursor: pointer;
    display: none;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
  }
}

.burger__close{
  display: none;
}

.nav{
  &__link{
    color: #fff;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    margin-left: 30px;
  }
}