.buttons{
  display: flex;
  justify-content: center;
  z-index: 200;
  .btn{
    margin-left: 6px;
    margin-right: 6px;
    &:first-child{
      margin-left: 0px;
    }
    &:last-child{
      margin-right: 0px;
    }
  }
}
.btn{
  border-radius: 50px;
  background-color: #FFE93F;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  transition: .4s;
  text-transform: uppercase;
  &.--big{
    height: 96px;
    padding-left: 40px;
    padding-right: 40px;
    font-size: 32px;
    line-height: 150%;
  }
  &.--small{
    height: 62px;
    padding-left: 32px;
    padding-right: 32px;
    font-size: 20px;
    line-height: 150%;
  }
}


