/* Main Colors */
$color-base : #111111;
$color-bg : #fff;
$color-text : $color-base;
$color-overlay : $color-base;
$opacity-overlay: .4;
$link-color : $color-base;


/* Fonts */
$font-family : 'Proxima Nova Rg', sans-serif;
$font-title: $font-family;
$font-size : 20px;
$font-weight : normal;
$letter-spacing : normal;
$line-height : 1.3;
$placeholder: #AAA3B4;



/*container*/
$container-padding-left: 20px;
$container-padding-right: 20px;
$container-width: 1160px + $container-padding-left + $container-padding-right;

